<template>
<transition name="fade">
<div class="modal-base">
  <div class="modal">
    <div class="container">
      <div class="close-result" @click="closeResult">&times;</div>
      <div class="result-wrap">
        <h3 class="text-sm-center">
          風險預測結果
        </h3>
        <div class="row">
          <div class="col-sm-3">
            <p class="text-sm-center risk-text">糖尿病風險</p>
            <canvas id="diabetes" width="100%"></canvas>
            <p class="text-sm-center risk-text">{{ this.riskDiabetes }} %</p>
          </div>
          <div class="col-sm-3">
          <p class="text-sm-center risk-text">冠狀動脈疾病風險</p>
            <canvas id="ascv" width="100%"></canvas>
            <p class="text-sm-center risk-text">{{ this.riskASCV }} %</p>
            </div>
          <div class="col-sm-3">
            <p class="text-sm-center risk-text">中風風險</p>
            <canvas id="stroke" width="100%"></canvas>
            <p class="text-sm-center risk-text">{{ this.riskStroke }} %</p>
            </div>
          <div class="col-sm-3">
            <p class="text-sm-center risk-text">高血壓風險</p>
            <canvas id="hypertension" width="100%"></canvas>
            <p class="text-sm-center risk-text">{{ this.riskHyper }} %</p>
          </div>
        </div>
      </div>
      <div class="suggestion-wrap">
        <h3 class="text-sm-center">給您的建議</h3>
        <p class="text-sm-center">{{ suggestion }}</p>
        <suggestion v-if="showSuggestion"></suggestion>
      </div>
    </div>
  </div>
</div>
</transition>
</template>

<script>
import Chart from 'chart.js'
import Suggestion from './Suggestion'

export default {
  props: ['showResult', 'risk'],
  name: 'Result',
  components: {
    Suggestion
  },
  data () {
    return {
      riskDiabetes: '',
      riskASCV: '',
      riskStroke: '',
      riskHyper: '',
      plotDiabetes: {
        datasets: [{
          data: '',
          backgroundColor: [
            '#1158a8',
            'rgb(224,224,224)'
          ]
        }]
      },
      plotASCV: {
        datasets: [{
          data: '',
          backgroundColor: [
            '#F7C242',
            'rgb(224,224,224)'
          ]
        }]
      },
      plotStroke: {
        datasets: [{
          data: '',
          backgroundColor: [
            '#90B44B',
            'rgb(224,224,224)'
          ]
        }]
      },
      plotHyper: {
        datasets: [{
          data: '',
          backgroundColor: [
            '#E87A90',
            'rgb(224,224,224)'
          ]
        }]
      },
      suggestion: '您在糖尿病的風險沒有很高，請繼續保持良好的生活習慣！',
      showSuggestion: false
    }
  },
  methods: {
    closeResult () {
      this.$emit('closeResult')
    }
  },
  created () {
    console.log('create')
    this.riskDiabetes = this.risk.diabetes
    this.riskASCV = this.risk.ascv
    this.riskStroke = this.risk.stroke
    this.riskHyper = this.risk.hypertension
    console.log(this.riskDiabetes)
  },
  mounted () {
    console.log('mounted')
  },
  watch: {
    riskDiabetes () {
      this.riskDiabetes = Math.round(this.risk.diabetes * 1000) / 10
      this.plotDiabetes.datasets[0].data = [this.riskDiabetes, 100 - this.riskDiabetes]
      if (this.riskDiabetes > 0.5) this.showSuggestion = true; this.suggestion = ''

      // diabetes
      const diabetes = document.getElementById('diabetes')
      this.chart = new Chart(diabetes, {
        type: 'doughnut',
        data: this.plotDiabetes,
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })
    },
    riskASCV () {
      // disease2
      this.riskASCV = Math.round(this.risk.ascv * 1000) / 10
      this.plotASCV.datasets[0].data = [this.riskASCV, 100 - this.riskASCV]

      // ASCV
      const ascv = document.getElementById('ascv')
      this.chart = new Chart(ascv, {
        type: 'doughnut',
        data: this.plotASCV,
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })
    },
    riskStroke () {
      this.riskStroke = Math.round(this.risk.stroke * 1000) / 10
      this.plotStroke.datasets[0].data = [this.riskStroke, 100 - this.riskStroke]

      // Stroke
      const stroke = document.getElementById('stroke')
      this.chart = new Chart(stroke, {
        type: 'doughnut',
        data: this.plotStroke,
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })
    },
    riskHyper () {
      this.riskHyper = Math.round(this.risk.hypertension * 1000) / 10
      this.plotHyper.datasets[0].data = [this.riskHyper, 100 - this.riskHyper]

      // Hypertension
      const hypertension = document.getElementById('hypertension')
      this.chart = new Chart(hypertension, {
        type: 'doughnut',
        data: this.plotHyper,
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })
    }
  }
}
</script>

<style lang="sass">
.modal-base
  z-index: 10
  position: fixed
  height: 100%
  width: 100%
  background-color: rgba(0,0,0, 0.5)
  top: 0
  display: flex
  .modal
    margin: auto
    width: 80%
    height: 80%
    background-color: white
    border-radius: 20px
    display: block
    position: relative
    .close-result
      position: absolute;
      right: 30px;
      top: 10px;
      font-size: 30px;
      z-index: 12
      transition: color 0.3s ease
      &:hover
        color: rgb(224,224,224)


    .result-wrap
      padding-top: 40px;
      h3
        padding-bottom: 10px

    .suggestion-wrap
      padding-top: 40px
      h3
        padding-bottom: 10px

.risk-text
  padding-top: 10px
  </style>



