          血液檢查
          <span v-if="BFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>

        <li :class="{ 'list-group-item': true, 'finished': DFinish }">
          肝功能
          <span v-if="DFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>

