<script type="text/javascript">
import axios from 'axios'
import Result from './Result'

export default {
  name: 'HDataPred',
  data () {
    return {
      A: {
        zgender: '',
        zage: '',
        aheight: '',
        aweight: '',
        astrsbp: '',
        astrdbp: '',
        abodyfat: '',
        awdth: ''
      },
      Other: {
        b10dr: '',
        c16wk: '',
        c19sport: '',
        smoker: '',
        d24dgkd: ''
      },
      B: {
        bhb: '',
        bhct: '',
        bwbc: '',
        bplt: '',
        cacsugar: ''
      },
      D: {
        dtbil: '',
        ddbil: '',
        dalp: '',
        dsgot: '',
        dsgpt: '',
        drgt: ''
      },
      F: {
        fbun: '',
        fcre: '',
        fua: ''
      },
      G: {
        gtg: '',
        gchol: '',
        jafp: '',
        lcrp: '',
        ghdlc: '',
        gldlc: ''
      },
      H: {
        af: '',
        lvf: '',
        cvd: '',
        parentCvd: ''
      },
      running: false,
      success: false,
      showResult: false,
      risk: {
        diabetes: '',
        ascv: '',
        stroke: '',
        hypertension: ''
      }
    }
  },
  components: {
    Result
  },
  methods: {
    defaultValue () {
      this.A.zgender = 2
      this.A.zage = 67
      this.A.aheight = 153
      this.A.aweight = 46.2
      this.A.astrsbp = '109'
      this.A.astrdbp = 52
      this.A.abodyfat = '22.1'
      this.A.awdth = 68

      this.Other.b10dr = 1
      this.Other.c16wk = 2
      this.Other.c19sport = 5
      this.Other.smoker = '0'  // System: is_smoker
      this.Other.d24dgkd = 1

      this.B.bhb = 13.9
      this.B.bhct = 40.6
      this.B.bwbc = 5.1
      this.B.bplt = 265
      this.B.cacsugar = 118

      this.D.dtbil = 0.68
      this.D.ddbil = 0.14
      this.D.dalp = 200
      this.D.dsgot = 17
      this.D.dsgpt = 17
      this.D.drgt = 36

      this.F.fbun = 15.1
      this.F.fcre = 0.7
      this.F.fua = 5

      this.G.gtg = 120
      this.G.gchol = 261
      this.G.jafp = 6
      this.G.lcrp = 0.2
      this.G.ghdlc = 53
      this.G.gldlc = 184

      this.H.lvh = '0'
      this.H.af = '0'
      this.H.cvd = '0'
      this.H.parentCvd = '0'
    },
    predict () {
      const instance = axios.create()
      const creds = {'conditions': [
        this.A.zgender,
        this.A.zage,
        this.A.aheight,
        this.A.aweight,
        this.A.astrsbp,
        this.A.astrdbp,
        this.B.bhb,
        this.B.bhct,
        this.B.bwbc,
        this.B.bplt,
        this.B.cacsugar,
        this.D.dtbil,
        this.D.ddbil,
        this.D.dalp,
        this.D.dsgot,
        this.D.dsgpt,
        this.D.drgt,
        this.F.fbun,
        this.F.fcre,
        this.F.fua,
        this.G.gtg,
        this.G.gchol,
        this.G.jafp,
        this.G.lcrp,
        this.G.ghdlc,
        this.G.gldlc,
        this.A.abodyfat,
        this.A.awdth,
        this.Other.b10dr,
        this.Other.c16wk,
        this.Other.c19sport
      ]}

      // set the running icon
      this.running = true

      instance.post('//metology.herokuapp.com/api/predict/diabetes/', creds)
        .then((response) => {
          this.risk.diabetes = response.data
          this.running = false
          this.success = true
          this.showResult = true
        })
        .catch(() => {
          console.log('failed')
        })

      const ascvCreds = {
        'age': this.A.zage,
        'is_diabetes': this.isDiabetes,
        'hdl_chol': this.G.ghdlc,
        'race': 'wh',
        'total_chol': this.G.gchol,
        'is_smoker': this.Other.smoker,
        'gender': (this.A.zgender - 1),
        'sbp': this.A.astrsbp,
        'is_hypertension': this.Other.d24dgkd
      }

      instance.post('//metology.herokuapp.com/api/predict/fram/ascvd10risk/', ascvCreds)
        .then((response) => {
          this.risk.ascv = response.data
        })

      const strokeCreds = {
        'is_lvh': this.H.lvh,
        'is_af': this.H.af,
        'is_hypertension': this.Other.d24dgkd,
        'is_smoker': this.Other.smoker,
        'is_diabetes': this.isDiabetes,
        'gender': (this.A.zgender - 1),
        'is_cvd': this.H.cvd,
        'sbp': this.A.astrsbp,
        'age': this.A.zage
      }

      instance.post('//metology.herokuapp.com/api/predict/fram/stroke10risk/', strokeCreds)
        .then((response) => {
          this.risk.stroke = response.data
        })

      const HypertensionCreds = {
        'dbp': this.A.astrdbp,
        'bmi': this.bmi,
        'is_parental_hypertension': this.H.parentCvd,
        'is_smoker': this.Other.smoker,
        'gender': (this.A.zgender - 1),
        'sbp': this.A.astrsbp,
        'age': this.A.zage
      }

      instance.post('//metology.herokuapp.com/api/predict/fram/hypertension_risk/', HypertensionCreds)
        .then((response) => {
          this.risk.hypertension = response.data
        })
    },
    closeResult () {
      this.showResult = false
    }
  },
  computed: {
    AFinish () {
      return checkFinished(this.A)
    },
    OtherFinish () {
      return checkFinished(this.Other)
    },
    BFinish () {
      return checkFinished(this.B)
    },
    DFinish () {
      return checkFinished(this.D)
    },
    FFinish () {
      return checkFinished(this.F)
    },
    GFinish () {
      return checkFinished(this.G)
    },
    allFinished () {
      if (this.AFinish && this.OtherFinish && this.BFinish && this.DFinish && this.FFinish && this.GFinish) return false
      return true
    },
    isDiabetes () {
      if (this.B.cacsugar > 126) return 1
      return 0
    },
    bmi () {
      return this.A.aheight / this.A.aweight / this.A.aweight
    }
  }
}

function checkFinished (item) {
  let isFinish = 0
  let keys = 0
  for (const key in item) {
    if (item[key] !== '') isFinish += 1
    keys += 1
  }
  if (isFinish === keys) return true
  return false
}

</script>

