      padding: 3%

.panel.panel-second
  border-color:  $color5
  .panel-heading
    background-color: $color5


.input-group
  padding-top: 3%

.app
  height: 100%
  width: 100%

.content
  position: relative
  width: 100%
  height: calc(100vh - 56px)

  .input-area
    padding: 10px 6%
    position: absolute
    width: calc(100% - 280px)
    height: 100vh
    overflow-y: auto
    &::-webkit-scrollbar
      display: none
    @media #{$small-screen}
      width: 100%

  .sidebar
    position: absolute
    width: 280px
    left: calc(100% - 280px)
    padding: 20px 40px
    height: 100vh
    background-color: $color1
    border-left: 1px solid gray
    transition: all 0.3s ease
    @media #{$small-screen}
      z-index: 2


    .logo
      display: flex
      width: 200px
      height: 200px
      border-radius: 50%
      background-color: white
      img
        margin: auto
        width: 90%
      @media #{$small-screen}
        display: none

    .HDataPred
      font-family: 'Proza Libre', serif;
      font-size: 30px
      color: white
      padding: 20px

    .list-group
      width: 200px
      box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
      color: $color5


  .btn-predict
    position: absolute;
    width: 200px;
    bottom: 20px;
    background-color: $color5
    border-color: $color5
    color: white
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
    transition: all 0.3s ease;
 
  .btn-default
    position: absolute;
    margin-top: 10px;
    width: 200px;
    background: transparent;
    border: 1px solid white;
    color: white;

  .finished
    color: $color4

  .success
    background-color: $color4
    border-color: $color4

</style>




