import Vue from 'vue'
import VueRouter from 'vue-router'
import Metology from './Metology'
import HdataPred from './components/HDataPred'
import Team from './components/Team'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    { path: '/', component: Metology },
    { path: '/HDataPred', component: HdataPred },
    { path: '/Team', component: Team }
  ]
})

/* eslint-disable no-new */
new Vue({
  router,
  template: `
  <div id="app">
    <transition name="fade" mode="out-in">
      <router-view ></router-view>
    </transition>
  </div>
  `
}).$mount('#app')



