<template>
  <div>
    <div class="container">
      <nav class="navbar navbar-light bg-faded">
        <a class="navbar-brand" href="/" style="color: rgb(17, 88, 168)">
          <img src="./assets/metology-icon.png">
          <span>METOLOGY</span>
        </a>
        <button class="navbar-toggler hidden-lg-up float-xs-right" type="button" data-toggle="collapse" data-target="#exCollapsingNavbar" aria-controls="exCollapsingNavbar" aria-expanded="false" aria-label="Toggle navigation"></button>
        <div class="collapse navbar-toggleable-md" id="exCollapsingNavbar">
          <ul class="nav navbar-nav float-xs-right">
            <li class="nav-item"><a href="#product" class="nav-link">產品</a></li>
            <li class="nav-item "><a href="" class="nav-link">聯絡我們</a></li>
            <li class="nav-item "><a href="" class="nav-link">Facebook專頁</a></li>
          </ul>
        </div>
      </nav>

    </div>
    <product></product>
    <hero></hero>
    <div class="container">
      <partner></partner>
    </div>
    <contact></contact>
  </div>
</template>

<script type="text/javascript">
import Hero from './components/Hero'
import Introduction from './components/Introduction'
import Member from './components/Member'
import Contact from './components/Contact'
import Product from './components/Product'
import Partner from './components/Partner'
import $ from 'jquery'

export default {
  name: 'Metology',
  components: {
    Hero,
    Introduction,
    Member,
    Product,
    Contact,
    Partner
  },
  data () {
    return {
      scrollPos: '',
      showMember: false
    }
  },
  methods: {
    scrollListen: function () {
      this.scrollPos = window.scrollY
      console.log(document.body.scrollTop)
      // console.log(document.body.)
      console.log(document.documentElement.scrollTop)
    },
    goto: function (id) {
      $('html, body').animate({ scrollTop: $('#' + id).offset().top }, 1000)
    },
    popMember: function () {
      this.showMember = true
    }
  },
  mounted: function () {
    // var a = $(".metology-header").height() - 10;

    $('.mdl-layout__content').scroll(function () {
      var scrollPos = $(this).scrollTop()
      var targetOpacity = 1
      scrollPos < 300 ? targetOpacity = scrollPos / 300 : targetOpacity
      $('.mdl-layout__header').css({
        'background-color': 'rgba(206, 203, 195, ' + targetOpacity + ')'
      })
      console.log(scrollPos, targetOpacity)
      // if ($(this).scrollTop() > 200) {
      //   $('.mdl-layout__header').css({'background': 'rgba(206,203,195, 0.9)'})
      // } else {
      //   $('.mdl-layout__header').css({'background': 'transparent'})
      // }
    })
    // if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50) {
    //   const head = document.getElementById('metology-header')
    //   head.style.background = 'rgb(206,203,195)'
    //   head.style.color = 'black'
    // }
  }
}

</script>

<style type="text/css" lang="sass">
@import '../node_modules/animate.css/animate.css';
@import url('https://fonts.googleapis.com/css?family=Libre+Baskerville:700');
@import url('https://fonts.googleapis.com/css?family=Raleway:400');
$color1: #0073e6
$color2: rgb(139,214,147)
$color3: rgb(71,196,194)


.navbar
  background: transparent;
  color: rgb(17, 88, 168)
  a
    span
      font-family: 'Libre Baskerville', serif;
      padding-left: 10px
    img
      width: 50px
      height: 50px
  .nav-item
    padding-top: 8px;

.navbar-nav
  @media screen and (max-width: 576px)
    float: none!important;
    margin-top: 3.6rem;

.navbar-toggler
  margin-top: 6px;

.navbar-toggleable-md .navbar-nav
  @media (max-width: 991px)
    margin-top: 3.5em



</style>



